                      <DatePicker :readonly="util.disabledFlag" class="date-input itemInput" @on-change="patientinfo.padTimeView.handover_time=$event" v-model="patientinfo.padTimeView.handover_time" type="datetime" :transfer="true" :editable = "true" format="yyyy-MM-dd HH:mm"></DatePicker>
