            </Form>
        </div>
         <div class="bg">时间汇总 </div>
        <div style="text-align:center" class="formStyle2">
            <Form ref="formValidate" :model="patientinfo.padTimeView" :rules="ruleValidate" :label-width="150">
                <ul class="ItemStyle">
                  <li class="ItemStyleLi">
