                      <DatePicker :readonly="util.disabledFlag" class="date-input itemInput" @on-change="patientinfo.padTimeView.help_date=$event" v-model="patientinfo.padTimeView.help_date" type="datetime" :transfer="true" :editable = "true" format="yyyy-MM-dd HH:mm"></DatePicker>

