                      <DatePicker :readonly="util.disabledFlag" class="date-input itemInput" @on-change="timeCompare('phep_ecg_time','ecg_diagnose_time',phepTime)" v-model="patientinfo.padTimeView.phep_ecg_time" type="datetime" :transfer="true" :editable = "true" format="yyyy-MM-dd HH:mm"></DatePicker>
