                      <DatePicker :readonly="util.disabledFlag" class="date-input itemInput" @on-change="patientinfo.padTimeView.tran_date=$event" v-model="patientinfo.padTimeView.tran_date" type="datetime" :transfer="true" :editable = "true" format="yyyy-MM-dd HH:mm"></DatePicker>

