                    </FormItem>
                  </li>
                   <li class="ItemStyleLi">
                    <FormItem label="初步诊断时间" prop="diagnosis_time" class="formItem" >
                      <DatePicker :readonly="util.disabledFlag" class="date-input itemInput" @on-change="patientinfo.padTimeView.diagnosis_time=$event" v-model="patientinfo.padTimeView.diagnosis_time" type="datetime" :transfer="true" :editable = "true" format="yyyy-MM-dd HH:mm"></DatePicker>

