                    </FormItem>
                  </li>
                   <li class="ItemStyleLi">
                    <FormItem label="进院时间" prop="arrived_hospital_time" class="formItem" >
                      <DatePicker :readonly="util.disabledFlag" class="date-input itemInput" @on-change="timeChangeTimeAxis($event,'arrived_hospital_time',1)" v-model="patientinfo.padTimeView.arrived_hospital_time" type="datetime" :transfer="true" :editable = "true" format="yyyy-MM-dd HH:mm"></DatePicker>
