</template>
<script>
import Vue from "vue";
export default {
  data() {
    return {
        timeOverView:{
          follow_car_doctor:"",//随车医生
          follow_car_nurse:"",//随车护士
          driver:'',//驾驶员
          attack_time:'',//发病时间
          help_date:"",//呼救时间
          start_car_time:"",//发车时间
          first_mc_time:"",//首次医疗接触
          phep_ecg_time:"",//首份心电图
          tran_date:"",//传输时间
          ecg_diagnose_time:"",//心电图诊断时间
          sampling_time:'',//抽血时间
          report_time:"",//报告时间
          diagnosis_time:"",//初步诊断时间
          acs_delivery_time:"",//首次药时间
          arrived_hospital_time:"",//进院时间
          prognosis_disease:"",//病情转归
          handover_time:"",//交接时间
          visit_result:"",//出诊结果
          handover_nurse:"",//接诊护士
          handover_doctor:"",//接诊医生
          delivery_hospital:"",//到达医院
        },
      phepTime:false, //院前首份心电图超时
      ecgSureTime:false, //院前首份心电图确诊时间超时
      reportTime:false,//抽血时间超时
      ruleValidate:{},
      ageList: [],//年龄单位
      peopleList:[],//随车人员
      hospitalList:[],//送达医院字典
    };
  },
  created(){
    var pThis=this;
    pThis.codeGet();
    pThis.GetBaseValue();
    pThis.timeCompare('first_mc_time','phep_ecg_time');
    pThis.timeCompare('phep_ecg_time','ecg_diagnose_time');
    pThis.timeCompare('sampling_time','report_time');
    if(pThis.patientinfo.padTimeView.ecg_diagnose_time_state =='1'){
      pThis.ecgSureTime=true;
    }
    if(pThis.patientinfo.padTimeView.phep_ecg_time_state =='1'){
      pThis.phepTime=true;
    }
    if(pThis.patientinfo.padTimeView.report_time_state =='1'){
      pThis.reportTime=true;
    }
//    console.log(pThis.patientinfo.padTimeView.phep_ecg_time)
  },
  mounted(){
      var pThis=this;
//    alert(pThis.patientinfo.AidButton);
//    pThis.patientinfo.AidButton = true;
//      beforeRouteLeave(to,from,next)
//      {
//        pThis.patientinfo.AidButton = false;
//        next();
//      }
  },
  methods:{
    scrollChange(ev){
      var div = document.getElementById('timeOver');
//        if (div.scrollHeight) {
//          var height = div.scrollHeight;
//          alert(height)
      setTimeout(function () {
        $("#timeOver").scrollTop(ev*40)
      }, 500)
//        }
    },
      timeCompare(date1,date2,value){
          var pThis = this;
          if(date1 !='' && date2 !=''){
              var firstMcTime = pThis.timeGet(pThis.patientinfo.padTimeView[date1]);
              var phepEcgTime = pThis.timeGet(pThis.patientinfo.padTimeView[date2]);
              if(date1 == 'sampling_time' && date2=='report_time'){
                if(phepEcgTime - firstMcTime > '20'){
                 pThis.reportTime = true;
                }else{
                  pThis.reportTime = false;
                }
              }else if(date1 == 'phep_ecg_time' && date2=='ecg_diagnose_time'){
                if (phepEcgTime - firstMcTime > '10') {
                  pThis.ecgSureTime = true;
                }else{
                  pThis.ecgSureTime = false;
                }
              }else if(date1 == 'first_mc_time' && date2=='phep_ecg_time'){
                if (phepEcgTime - firstMcTime > '10') {
                  pThis.phepTime = true;
                }else{
                  pThis.phepTime =false;
                }
              }
          }
      },
    timeGet(date){
          var newDate = new Date(date);
          var year = newDate.getFullYear();
          var month = newDate.getMonth()+1;
          var day = newDate.getDate();
          var hour = newDate.getHours(); //获取系统时，
          var minute = newDate.getMinutes(); //分
      if(hour-9<=0){
        hour = "0" + hour
      }
      if(minute-9<=0){
        minute = "0" + minute
      }
          return year +''+ month +'' +day +''+ hour +''+ minute;
    },
    //通过日历时间转时间轴---参数（时间，key，时间轴的index）
    timeChangeTimeAxis(ev,str,index){
      var pThis=this;
      pThis.timeCompare('first_mc_time','phep_ecg_time');
      if(ev && typeof ev=="string"){
        pThis.$emit("time-change",ev,str,index);
      }else{
        pThis.$emit("time-change",pThis.patientinfo.padTimeView[str],str,index);
      }
    },
    CheckChange(value){   //全局变量值交换
      var pThis = this;
      pThis.patientinfo.padTimeView[value]= pThis.baseInfo[value];
    },
    GetBaseValue(){   //全局变量赋值
      var pThis = this;
      for(var value in pThis.patientinfo.padTimeView){
        pThis.timeOverView[value] = pThis.patientinfo.padTimeView[value];
      }
    },
    codeGet(){  //字典查询
      var pThis = this;
      var codes = {code:["CARFOLLOWING","HOSPITAL"]};// 随车人员
      pThis.$http.post(pThis.host('defdoc/getDefdocViewByCodes'),codes)
        .then(function (res) {
          let datajson = res.data;
          let backjson={"state":datajson.state,"msg":datajson.msg};
          pThis.backLogin(backjson);
          if(datajson.state=='1'){
            var data=datajson.results[0];
            for(var key in data){
              if(key=='CARFOLLOWING'){//随车人员
                pThis.peopleList=data[key];
              };
              if(key=='HOSPITAL'){//随车人员
                pThis.hospitalList=data[key];
              }
            }
          }
        }).catch(function (error) {
        console.log(error)
      });
    },
    //dateTime
    dateTime(date,length){
      if(length==10){
        if(typeof date =='object'){
          var years=date.getFullYear();
          var months=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
          var days=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
          return years+"-"+months+"-"+days;
        }
      }else{
        if(typeof date =='object'){
          var year=date.getFullYear();
          var month=date.getMonth()+1 <=9 ?"0"+(date.getMonth()+1):date.getMonth()+1;
          var day=date.getDate() <=9 ?"0"+date.getDate():date.getDate();
          var hours=date.getHours() <=9 ?"0"+date.getHours():date.getHours();
          var mins=date.getMinutes() <=9 ?"0"+date.getMinutes():date.getMinutes();
          var seds=date.getSeconds() <=9 ?"0"+date.getSeconds():date.getSeconds();
          return year+"-"+month+"-"+day+" "+hours+":"+mins+":"+seds;
        }
      }
      return date
    },
  },
};
</script>
<style lang="scss" scoped>
  .timeOver {
    font-size: 20px;
    background-color: white;
    height: 100%;
    overflow-y: auto;
  }
