  padding-left: 24px;
  border-bottom: 1px solid #999;
  .timeBtn {
    outline: none;
    background: #fff;
    width: 150px;
    height: 40px;
    line-height: 40px;
    border-radius: 20px;
    border: 1px solid #999;
    margin-bottom: 40px;
  }
}
  .bg {
    font-weight: bold;
    padding-left: 20px;
    display: block;
    background: white;
    height: 46px;
    font-size: 16px;
    line-height: 46px;
    color: #173f99;
    border-bottom: 1px solid #eBeaf1;
    font-family: "微软雅黑";
  }


.formStyle {
  font-size: 18px;
  // overflow: hidden;
  overflow-x: hidden;
  /*overflow-y: auto;*/
  /*margin-top: 10px;*/
  padding-top: 10px;
  /*padding-left: 30px;*/
  background-color: white;
  // margin-bottom: 20px;
  // margin-top: 20px;
  .diseaseCheck .diseaseLi{
    width: 120px;
    display: inline-block;
  }
  .ItemStyle {
    position: relative;
    // margin-top: 10px;

    .ItemStyleLi {
      height: 40px;
      width: 300px;
      // margin-bottom: 10px;
      float: left;
      /*margin-right: 20px;*/
      line-height: 33px;
      // height: 35px;
      .itemInput{
        width: 200px;
      }
      .formItem {
        width: 254px;
      }
      .w120 {
        width: 180px !important;
      }
    }
  }
}
.formStyle2 {
  font-size: 18px;
  // overflow: hidden;
  overflow-x: visible;
  overflow-y: visible;
  margin-top: 10px;
  // margin-bottom: 20px;
  // margin-top: 20px;
  .ItemStyle {
    position: relative;
    // margin-top: 10px;

    .ItemStyleLi {
      width:50%;
      height: 40px;
      // margin-bottom: 10px;
      float: left;
      /*margin-right: 20px;*/
      line-height: 33px;
      // height: 35px;
      .itemInput{
        width: 200px;
      }
      .formItem {
        width: 304px;
      }
      .w120 {
        width: 180px !important;
      }
    }
  }
}
/deep/ .ivu-form-item-content {
  text-align: left;
}
</style>




